  --color-dark: #282D31;
  --color-dark-alt: #141618;
  --color-border: #DFE0E0;
  --color-text: #53575A;
  --color-text-light: #BABDBF;
  --color-body-bg: #F6F6F6;
}

.color {
  color: var(--color);
}

.background {
  background-color: var(--color);
}

.first-color, .color-first {
  --color: var(--first-color);
  --color-alt: var(--first-color-alt);
}

.second-color, .color-second {
  --color: var(--second-color);
  --color-alt: var(--second-color-alt);
}

.third-color, .color-third {
  --color: var(--third-color);
  --color-alt: var(--third-color-alt);
}

.accent-color, .color-accent {
  --color: var(--accent-color);
  --color-alt: var(--accent-color-alt);
}

.dark-color, .color-dark {
  --color: var(--dark-color);
  --color-alt: var(--dark-color-alt);
}

.white-color, .color-white {
  --color: #FFF;
}

.text-color, .color-text {
  --color: var(--text-color);
  --color-alt: var(--dark-color);
}

.light-color, .color-light {
  --color: var(--light-color);
  --color-alt: var(--border-color);
}

.first-color-alt {
  --color: var(--first-color-alt);
}

.second-color-alt {
  --color: var(--second-color-alt);
}

.third-color-alt {
  --color: var(--third-color-alt);
}

.accent-color-alt {
  --color: var(--accent-color-alt);
}

.dark-color-alt {
  --color: var(--dark-color-alt);
}

.border-color {
  --color: var(--border-color);
}

.text-color {
  --color: var(--text-color);
}

.body-bg {
  --color: var(--body-bg);
}

.body-bg.color-card [class*=ratio] {
  border: 1px solid var(--border-color);
  border-bottom: none;
}

.box-color {
  background: #cccccc;
  color: #666666;
  border: 1px solid white;
  padding: 5px 15px;
}

.section-divider {
  padding-bottom: 30px;
}

img {
  border: 0;
  display: block;
  width: 100%;
  max-width: 100%;
  height: auto;
}
